.image-gradient-dynamic {
  line-height: 0;
  position: relative;
  width: 100%;
}

.image-gradient-dynamic img {
  height: auto;
  left: 0;
  position: relative;
  top: 0;
  width: 100%;
}

.image-gradient-dynamic .copy {
  left: 50%;
  margin: auto;
  position: absolute;
  text-align: center;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 999;
}

.image-gradient-dynamic .copy p {
  color: white;
  line-height: 1.5em;
  padding: 1em 2em;
  position: relative;
}

.image-gradient-dynamic .overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(255, 0, 0, 0.1);
  background-image: linear-gradient(0deg, rgba(255, 0, 0, 0.1), rgba(0, 128, 0, 0.4));
  display: block;
}

.image-gradient-dynamic .overlay::after {
  position: absolute;
  top: 1000px;
  right: 1000px;
  bottom: 1000px;
  left: 1000px;
  background-color: rgba(0, 128, 128, 0.2);
  background-image: linear-gradient(90deg, rgba(0, 128, 128, 0.2), rgba(255, 255, 0, 0.2));
  content: "";
  display: block;
}

